function loadRecipeDetails(recipeId) {
  fetch(`/recipes/${recipeId}`)
    .then(response => response.json())
    .then(data => {
      document.getElementById('modalTitle').textContent = data.title;
      document.getElementById('modalContent').textContent = data.description; // 仮のプロパティ名
      document.getElementById('recipeDetailsModal').showModal();
    })
    .catch(error => console.error('Error loading recipe details:', error));
}

window.loadRecipeDetails = loadRecipeDetails;